import page_header from "@/components/basic_components/page_header/page_header.vue";
import skills_card from "@/components/skills/skills_card/skills_card.vue";

export default {
	components: {
		page_header,
		skills_card,
	},

	data() {
		return {
			generating_pdf: false,
			company_name: "AppsCantFly",
			company_place: "Hengelo",
			company_kvk: "69371326",
			company_website: "rickrosendaal.nl",
			quotation_validity_period: "2 maanden",
		}
	},
	methods: {
		generate_pdf() {
			this.generating_pdf = true;
			const opt = {
				margin: [0.25, 0.5],
				filename: 'Algemene voorwaarden - AppsCantFly.pdf',
				image: {type: 'jpeg', quality: 1},
				html2canvas: {scale: 2},
				jsPDF: {unit: 'in', format: 'letter', orientation: 'portrait'},
				pagebreak: {avoid: 'section'},
			};
			const html2pdf = require('html2pdf.js');
			html2pdf().set(opt).from(document.getElementById('terms-and-conditions-contents')).save().then(() => {
				this.generating_pdf = false;
			});
		}
	}
}
